.image__background {
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
  object-fit: cover;
  z-index: -1;
}
.body__content {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 200px;
  margin-right: 600px;
}
.quote {
  border-radius: 10px;
  font-style: italic;
  font-size: 1.6em;
  margin: 20px 0;
  padding: 10px 20px;
  border-left: 5px solid #ccc;
  color: #555;
  background-color: #f9f9f9;
  quotes: "“" "”" "‘" "’";
}
.quote ::before {
  content: open-quote;
  font-size: 2em;
  color: #ccc;
  vertical-align: -0.4em;
  margin-right: 5px;
}

.quote ::after {
  content: close-quote;
  font-size: 2em;
  color: #ccc;
  vertical-align: -0.4em;
  margin-left: 5px;
}
.author {
  font-style: italic;
  font-size: 1.6em;
  align-self: self-end;
  margin: 10px;
}
